@import '../variables/colors';
.chart{
  // margin-top:10em;
  display:flex;
  // align-items: center;
  height:30vh;
  // @media only screen 
  // and (max-width: $break-1440) 
  // and (orientation:landscape) {
  //   flex-flow: column;
  // }
  
  @include respond-to(md-screen){
    height:65vh;
    margin:2.5em 0;
  }
  @include respond-to(tablet-xl){
    flex-flow: column;
    min-height:100vh;
    margin:-5em 0 10em 0;
  }
  @include respond-to(tablet){
    flex-flow: column;
    min-height:100vh;
    margin:-5em 0 8em 0;
  }
  @include respond-to(tablet-xs){
    flex-flow: column;
    min-height:100vh;
    margin:-3em 0 4em 0;
  }
  &__pie{
    //min-width:50em;
    margin:0 auto;
    align-self: center;
    transform: scale(1.7);
    -webkit-transform: scale(1.7);
    -moz-transform: scale(1.7);
    -ms-transform: scale(1.7);
    -o-transform: scale(1.7);
    @include respond-to(lg-screen){
      transform: scale(1.2);
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      -ms-transform: scale(1.2);
      -o-transform: scale(1.2);
}
    @include respond-to(md-screen) {
      transform: scale(1);
      -webkit-transform: scale(1);
      -moz-transform: scale(1);
      -ms-transform: scale(1);
      -o-transform: scale(1);
    }
  @include respond-to(tablet-xl) {
    height:20vh;
    transform: scale(.7);
    -webkit-transform: scale(.7);
    -moz-transform: scale(.7);
    -ms-transform: scale(.7);
    -o-transform: scale(.7);
}
}
  &__scatter{
    height:100%;
    font-size:3rem;
    min-width:30vw;
    transform: scale(.7);
      -webkit-transform: scale(.7);
      -moz-transform: scale(.7);
      -ms-transform: scale(.7);
      -o-transform: scale(.7);
    @include respond-to(md-screen){
      font-size:1rem;
    }
      @include respond-to(md-screen){
        font-size:1rem;
        min-width:65vw;
        font-size:2rem;
        transform:scale(1)      ;
        -webkit-transform:scale(1)      ;
        -moz-transform:scale(1)      ;
        -ms-transform:scale(1)      ;
        -o-transform:scale(1)      ;
      }
      @include respond-to(tablet-xl) {
        font-size:.9rem;
        min-height:100%;
        min-width:100vw;
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
        -moz-transform: scale(1.2);
        -ms-transform: scale(1.2);
        -o-transform: scale(1.2);
}
      @include respond-to(tablet){
        
        min-height:100vh;
        min-width:100vw;
        transform: scale(1.125);
        -webkit-transform: scale(1.125);
        -moz-transform: scale(1.125);
        -ms-transform: scale(1.125);
        -o-transform: scale(1.125);
}
    @include respond-to(tablet-xs){
      
    }
  }
}