@import '../variables/colors';

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 0;
  background-color: $color-main;
  &__privacy-policy{
    color: $color-light;
    text-decoration: none;
  }
  &__text{
    position: relative;
    z-index: 20;
    display:grid;
    place-content: center center;
    height:100%;
    color: $color-light;
    &-container{
      text-align:center;
      max-width: 1080px;
      margin:0 2em .5em 2em;
    }
    &-headline{
      font-size: 5rem;
      font-weight:200;
      
      @include respond-to(tablet-landscape){
        font-size:3rem;
      }
      @include respond-to(tablet-landscape-wide){
        font-size:4rem;
      }
      @include respond-to(mobile-landscape){
        font-size:2.3rem;
      }
      @include respond-to(mobile-portrait-xs){
        font-size:2.5rem;
      }
    }
    &-copy{
      font-size:1.5rem;
      font-weight:400;
      @include respond-to(tablet-landscape){
        font-size:1.2rem;
      }
      @include respond-to(mobile-landscape){
        font-size:1.3rem;
        margin-bottom:1em;
      }
      @include respond-to(mobile-portrait){
        font-size:1.2rem;
        margin-bottom:3em;
      }
    }
  }
  
}
.login-button {
  text-transform: uppercase;
  text-decoration: none;
  border: 4px solid $color-dark;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
  -webkit-transition: all 0.4s ease 0s;
  -moz-transition: all 0.4s ease 0s;
  -ms-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: $color-lighter;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 $color-dark;
}
.google-btn:hover {
  box-shadow: 0 0 6px $color-lighter;
}

.google-btn:active {
  background: $color-lighter;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: $color-light;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  color: $color-lighter;
  font-size: 14px;
  font-weight: 800;
  padding:.2em;
  letter-spacing: 0.2px;
  background-color: $color-dark;
  width: 80%;
  height: 100%;
  text-align: center;
}