@mixin respond-to($media){
  @if $media == md-screen{
    @media only screen 
    and (max-width: $break-1440) 
    and (orientation:landscape) 
    { @content; } 
  }
  @else if $media == lg-screen{
    @media only screen 
      and (max-width: $break-2880 + 1) 
      and (orientation:landscape) 
        { @content; } 
    
  }
  @else if $media == lgr-screen{
    @media only screen 
      and (max-width: $break-5120 + 1) 
      and (orientation:landscape) 
        { @content; } 
    
  }
  
  @else if $media == desktop{
    @media only screen 
      and (max-width: $break-900 ) 
      and (orientation:landscape) 
        { @content; } 
    
  }
  
  //! TABLET/LARGE PHONES
  @else if $media == tablet-xl{
    @media only screen 
      and (max-width: $break-1024 - 1) 
      and (orientation:landscape) 
        { @content; } 
    
  }
  @else if $media == tablet{
    @media only screen 
      and (max-width: $break-640) 
      and (orientation:landscape) 
        { @content; } 
    
  }
  
  @else if $media == tablet-xs{
    @media only screen 
      and (max-width: $break-533) 
      and (orientation:landscape) 
        { @content; } 
    
  }
