.Alert{
  p{
    // font-size:1.2em;
    font-weight: 800;
    @include respond-to(sm-screen){
      font-size:1em;
    }
    @include respond-to(xs-screen){
      font-size:.8em;
    }
  }
  &__online{
    &-active{
      top:1.3em;
      left:45%;
      display:inline;
      position:absolute;
      font-size:2rem;
      background:$color-lighter;
      padding: .5em;
      color:$color-main;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
      @include respond-to(mobile-portrait-xs){
        top:1.7em;
        left:13%;
      }
    }
    &-hidden{
      top:-3em;
      
    }
    
}
  &__numberofevents{
    position: relative;
    top:-.2em;
    min-height:1.5em;
  }
  &__citysearch{
    @include respond-to(mobile-portrait){
      min-height:3rem;
    }
    &-info{
      min-height:2.2rem;
      z-index: -1;
      position: absolute;
      margin:1em 0;
      max-width:100%;
      min-height:3em;
      display:inline;
      @include respond-to(tablet-landscape-wide){
        position: relative;
        bottom:-1em;
      }
      @include respond-to(tablet-landscape){
        position: relative;
        bottom:-7em;
      }
      @include respond-to(mobile-landscape){
        position: relative;
        top:7.4em;
      }
      @include respond-to(mobile-landscape){
        position: relative;
        top:7.4em;
      }
      
      @include respond-to(tablet-portrait){
        top:38.5%;
      }
      @include respond-to(tablet-portrait-wide){
        top:33%;
      }
      @include respond-to(tablet-portrait-wide-xl){
        top:30%;
      }
      
      @include respond-to(mobile-portrait-xl){
        top:initial;
        bottom:40%;
      }
      @include respond-to(mobile-portrait){
        top:14em;
        position: absolute;
      }
      @include respond-to(mobile-portrait-xs){
        position: absolute;
        top:56%;
      }
    }
  }
}

@keyframes slidein {
  from {
    top:-2em;
  }
  to{
    top:0;
  }
}