@import '../variables//colors';
@import '../variables/font';
.suggestions{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  align-items:stretch;
  max-width:80%;
  min-height:5em;
  position: relative;
  li {
    flex-wrap:wrap;
    list-style: none;
    &:nth-child(even){
      color: $color-lighter;
    }
    &:hover{
      color: $color-dark;
      cursor: pointer;
    }
    max-width:100%;
    flex-grow: 1;
    color: $color-light;
    font-weight: 800;
    margin:.3em 0;
    font-size: $suggestion-text;
  }
  button{
    // flex-grow: 1;
    z-index: 200;
    top:-5.5em;
    right:1.7em;
    margin: 1em 0 1em 0;
    @extend button-element;
    @include respond-to(mobile-portrait){
      margin: 0 0em 2em 0;
    }
  }
}