 @import '../variables/colors';
 .footer{
  display:flex;
  flex-flow:column;
  align-items:center;
  min-width: 100%;
  // padding: 3em;
  //background-color: $color-main-2;
  position: fixed;
  bottom:0;
  z-index: 0;
  min-height:5vh;
  //background: rgba( 32, 72, 138, 0.45 );
  background:hsla(75, 80%, 86%, .15);
  //box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, .09) 0px -3px 5px;
  backdrop-filter: blur( 3.5px );
  -webkit-backdrop-filter: blur( 3.5px );
  &__container{
    display:flex;
    justify-content: space-evenly;
    width: 100%;
  }
  &__link{
    margin-top:1em;
    text-decoration: none;
    font-size:1.6rem;
    font-weight: 200;
    color: $color-light;
    &-citation{
      color:$color-light;
      font-size:.5rem;
      text-decoration: none;
      font-weight: 800;
      margin:2em auto;
    }
  }
}
