@import '../variables/colors';
@import '../variables/font';
input-element {
  background-color: $color-light;
  border: 1px $color-dark solid;
  padding:1em;
  height: 3rem;
  width:80%;
  border-radius: 4px;
  font-size:$input-text;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  @include respond-to(lg-screen){
    // font-size:1.8rem;
  }
  @include respond-to(md-screen){
    // font-size:1.5rem;
    min-width: 1em;
    padding:.2em;
  }
}
input-element:focus{
  background-color: $color-lighter;
  border: 1px $color-light solid;  
}