@import '../variables/colors';
@import '../variables/font';
@import '../elements/input';
@import '../elements/button';

//? event number
.display{
  &-event-number{
    font-size: $font-5;
    color:$color-lighter;
    @include respond-to(lg-screen){
      font-size: $font-3;
      margin: 1em 0;
    }
  }
}

//? main container
.NumberOfEvents{
  padding:6em 1em;
  &__input-container{
  }
  &__label{
    font-size: $font-4;
    display:block;
    color:$color-light;
    margin-bottom:.4em;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
  // @include respond-to(md-screen){
  //   // display:flex;
  //   justify-content: space-between;
  // }
  input {
    @extend input-element;
    @include respond-to(lg-screen){
      width: 40%;
    }
  }
  @include respond-to(md-screen){
    padding:0;
  }
}