@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/invertColor';
@import '../elements/input';
@import '../elements/button';
.CitySearch {
  padding:2em 2em 0 2em;
  color: $color-lighter;
  &__heading{
    display:flex;
    align-items: center;
  }
  &__err{
    color:green;
    font-size: 3rem;
    z-index: -1;
    position: absolute;
    margin:1em 0;
    min-height: 100%;
    top:7em;
    max-width:100%;
  }
  &__label{
    font-size: $font-4;
    display:block;
    color:$color-light;
    margin-bottom:.4em;
    margin-right:2em;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
  
  &__btn {
    margin: 1em 0 .5em 0;
    @extend button-element;
  }
  
  input{
    @extend input-element;
    @include respond-to(lg-screen){
      font-size: $font-3;
    }
  }
  @include respond-to(md-screen){
    padding:0;
  }
}

