@import '../variables/colors';
@import '../variables/font';
@import '../abstracts/invertColor';
@import '../elements/input';
@import '../elements/button';
ul.EventList {
  // padding:0 2em 10em 2em;
  // width: 50vw;
  display:grid;
  grid-template-columns: 1fr 1fr;
  gap:3em;
  overflow-y: scroll;
  // overflow-x: hidden;
  margin: 0 auto;
  max-height:100vh;
  // height:100vh;
  @include respond-to(lg-screen){
    grid-template-columns: .5fr .5fr;
  }
  @include respond-to(md-screen){
    max-height:none;
    overflow-y: visible;
    grid-template-columns: 1fr 1fr;
  }
  @include respond-to(tablet-xl){
    margin-top:15em;
  }
  li {
    list-style-type: none;
    width: 100%;
  }
}
