@import '../variables/font';
@import '../variables/colors';
@import '../elements/button';


.Event{
  background-color: $color-lighter;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, .09) 0px -3px 5px;
  padding: 1em 4em;
  margin: .8em 0;
  list-style-type: none;
  width: calc(50vw/2);
  @include respond-to(md-screen){
    width: calc(85vw/2);
  }
  @include respond-to(tablet-xl){
    padding:1em;
  }
  @include respond-to(tablet-xs){
    padding:.5em;
  }
  &-date, &-location{
    font-weight: 800;
    font-size:$card-text;
  }
}

.event{
  &-icon{
    transform: scale(5);
    -webkit-transform: scale(5);
    -moz-transform: scale(5);
    -ms-transform: scale(5);
    -o-transform: scale(5);
    @include respond-to(lg-screen){
      transform: scale(2.5);
      -webkit-transform: scale(2.5);
      -moz-transform: scale(2.5);
      -ms-transform: scale(2.5);
      -o-transform: scale(2.5);
}
}
  &-details-hide{
    display:none;
    // max-width:100vw;

  }
  &-details-show{
    display:block;
    height:100%;
    max-width:100%;
    
  }
  
  &-title{
    font-size:$card-heading;
    font-style: italic;
    &-container{
      display:flex;
      align-items: baseline;
      justify-content:space-between;
    }
  }
  
  &-description{
    font-size:$card-text;
  }
  
  
  &-link{
    @extend button-element;
    font-weight: 800;
    padding:.3em;
    text-decoration: none;
    display:inline-block;
    margin: 2em 0;
    overflow:hidden;
    color: $color-main-1;
    word-wrap: break-word;
    max-width: 100%;
    cursor: pointer;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    &:hover{
      color:$color-lighter;
    }
  }
}