@import '../variables/colors';
button-element {
  font-family: 'houschka-rounded';
  border-radius: 8px;
  padding:.4em;
  font-size: $btn-text;
  background-color:$color-dark;
  color: $color-light;
  border:none;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  box-shadow: 0 20px 30px -10px #26394D;
}